import "./App.css";
import Navbar from "./components/Navbar";
import TextForm from "./components/TextForm";
import About from "./components/About";
import { useState } from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
function App() {
  // By default it should for dark mode
  const [Style, setStyle] = useState({
    color: "white",
    backgroundColor: "#212529",
    btnColor: "dark",
    btnText: "Enable Light Mode",
  });

  const ToggleSwitch = () => {
    switch (Style.color) {
      case "white":
        setStyle({
          color: "black",
          backgroundColor: "white",
          btnColor: "light",
          btnText: "Enable Dark Mode",
        });
        break;
      case "black":
        setStyle({
          color: "white",
          backgroundColor: "#212529",
          btnColor: "dark",
          btnText: "Enable Light Mode",
        });
        break;
      default:
        break;
    }
  };

  document.body.style = {
    backgroundColor: Style.backgroundColor,
    color: Style.color,
  };

  return (
    <>
      <Router>
        <Navbar
          btnColor={Style.btnColor}
          btnText={Style.btnText}
          toggle={ToggleSwitch}
        />
        <Switch>
          <Route exact path="/text-utils-website">
            <div className="container my-3">
              <TextForm styles={Style} />
            </div>
          </Route>
          <Route exact path="/about">
            <About />
          </Route>
        </Switch>
      </Router>
    </>
  );
}

export default App;
