import React, { useState } from "react";

export default function TextForm(props) {
  const [text, setText] = useState("");
  const Style = props.styles;

  document.body.style.backgroundColor = Style.backgroundColor;
  document.body.style.color = Style.color;

  function handleOnChange(event) {
    setText(event.target.value);
  }
  function handleOnUpperCaseClick() {
    setText(text.toUpperCase());
  }
  function handleOnLowerCaseClick() {
    setText(text.toLowerCase());
  }
  function handleOnClearClick() {
    setText("");
  }
  function handleOnCopyClick() {
    if (!text) return;
    navigator.clipboard.writeText(text);
  }


  return (
    <>
      <div className="mb-3">
        <h1>{props.heading}</h1>
        <textarea
          className="form-control"
          id="myBox"
          rows="8"
          value={!text ? "" : text}
          onChange={handleOnChange}
          itemType="password"
          placeholder="Enter text here"
          style={{ color: Style.color, backgroundColor: Style.backgroundColor }}
        ></textarea>
        <button
          className={`btn btn-${Style.btnColor} btn-outline-${Style.btnColor === "dark" ? "light" : "dark"
            } my-2 mx-1`}
          onClick={handleOnUpperCaseClick}
        >
          Convert to Uppercase
        </button>
        <button
          className={`btn btn-${Style.btnColor} btn-outline-${Style.btnColor === "dark" ? "light" : "dark"
            } my-2 mx-1`}
          onClick={handleOnLowerCaseClick}
        >
          Convert to Lowercase
        </button>
        <button
          className={`btn btn-${Style.btnColor} btn-outline-${Style.btnColor === "dark" ? "light" : "dark"
            } my-2 mx-1`}
          onClick={handleOnClearClick}
        >
          Clear
        </button>
        <button
          className={`btn btn-${Style.btnColor} btn-outline-${Style.btnColor === "dark" ? "light" : "dark"
            } my-2 mx-1`}
          onClick={handleOnCopyClick}
        >
          Copy
        </button>

      </div>

      <div className="container my-3 text-center" id="text-summary">
        <h1>Text Summary</h1>
        <p>
          {text.split(" ").length - 1}{" "}
          {[0, 1].includes(text.split(" ").length - 1) ? "word" : "words"} and{" "}
          {text.length}{" "}
          {[0, 1].includes(text.length) ? "character" : "characters"}
        </p>
      </div>

      <div className="container my-3 text-center">
        <h1>Text Preview</h1>
        <p id="text-preview-code-tag">
          <code
            style={{
              backgroundColor: Style.backgroundColor,
              color: Style.color,
            }}
          >
            {!text ? "No Preview available" : text}
          </code>
        </p>
      </div>
    </>
  );
}
